	export default {
		name: 'glanceSlideNavTabBar',
		props: {
			//载入的标签数据
			data: Array,
			//组件背景颜色
			bgcolor:{
				type:String,
				default: '#FFFFFF'
			},
			//是否启用顶吸
			topfixed:{
				type: Boolean,
				default: false
			},
			//距离顶部顶吸的距离
			topfixedval:{
				type: String,
				default: '0px'
			},
			//字体大小
			fontsize:{
				type: String,
				default: '13px'
			},
			//文字默认初始化颜色
			textcolor:{
				type:String,
				default:'#999999'
			},
			//活动的项目文字颜色
			activetextcolor:{
				type: String,
				default: '#000000'
			},
			//下划线颜色
			underlinecolor:{
				type: String,
				default: '#6495ED'
			},
			//下划线高度
			underlineheight:{
				type: String,
				default: '10rpx'
			},
			//下划线宽度
			underlinewidth:{
				type: String,
				default: '35%'
			}			
		},
		data() {
			return {
				//当前活动的item
				activeitemindex:0,
				// 滚动到点击的项目位置 初始化第一个
				curscrollitem: 'glsntb-0',
				selfdata:[
					{
						textcontent:'',//文字内容
						activeviewid:''//当前活动的子标签id
					}
				]
			};
		},
	computed: {
		gettabbars(){
			//主组件需要:data = [{},{}]格式导入标签对象
			let tabitems = []
			this.data && this.data.forEach((item, index) => {
				tabitems.push(item)
				}
			)
			return tabitems
		},
		// 如果启用顶吸 则返回顶吸设置
		positionS(){
			if (this.topfixed){
				return 'sticky'
			}else{
				return 'static'
			}
		}
	},
	methods:{
		// 点击项目事件处理
		clickitem(idx,val){
			// 设置当前点击的项目id
			this.curscrollitem = 'glsntb-' + idx
			// 设置当前活动的项目索引值
			this.activeitemindex = idx
			// 父组件通信事件
			this.$emit('clickitem',idx,val)
		}
	}
}
